const useCustomFunctions = () => {
  const dateTimeFormat = dateTime => {
    // time = "2017-01-18T17:02:09.000+05:30"
    if (!dateTime) return ''
    const t = new Date(dateTime)
    const formattedDateTime = t.toLocaleString('es-MX', {
      // weekday: 'long',
      year: 'numeric',
      // month: 'long',
      month: '2-digit',
      day: '2-digit',
      hour: 'numeric',
      minute: 'numeric',
      second: 'numeric',
      // hour12: true,
    })
    return formattedDateTime
  }

  const dateStringToFormatYMDH = dateString => {
    const dateArray = dateString.split('-')
    dateArray.reverse()
    const year = dateArray[0].trim()
    const month = dateArray[1].trim().padStart(2, '0')
    const day = dateArray[2].trim().padStart(2, '0')
    const dateFormat = `${year}-${month}-${day}`
    return dateFormat
  }

  const dateObjectoToString = date => {
    const year = date.getFullYear()
    const month = String(date.getMonth() + 1).padStart(2, '0')
    const day = String(date.getDate()).padStart(2, '0')
    const hours = String(date.getHours()).padStart(2, '0')
    const minutes = String(date.getMinutes()).padStart(2, '0')
    return `${year}-${month}-${day} ${hours}:${minutes}`
  }

  return {
    dateTimeFormat,
    dateStringToFormatYMDH,
    dateObjectoToString,
  }
}

export default useCustomFunctions
